
.list-inline li {
    display: inline-block;
    padding: 0 10px 10px 0
}

.list-inline label {
    background: #fff;
    min-height: 50px;
    border: 1px solid #eaeaea;
    border-radius: 2px;
    display: inline-block;
    margin: 0 2px 0 0;
    padding: 14px 15px 12px 15px;
    line-height: 1.4;
    -webkit-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, .07);
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, .07);
    cursor: pointer
}

.list-inline label .checkbox,
.list-inline label .radio {
    vertical-align: top;
    position: relative;
    top: 1px
}

.list-inline label:active,
.list-inline label:focus {
    background: rgba(0, 0, 0, .02)
}

.list-inline label .radio .input-helper:before {
    top: -1px
}

.list-inline label .checkbox .input-helper:after,
.list-inline label .radio .input-helper:after {
    top: 4px
}

.list-inline label .checkbox .input-helper:before {
    top: -2px
}

.list-inline.list-inline--onehalf {
    margin: 0 0 0 -20px
}

.list-inline.list-inline--onehalf li {
    padding: 0 0 10px 20px;
    width: 50%;
    margin: 0 -4px 0 0
}

.list-inline.list-inline--onehalf li label {
    display: block
}

.list-onethird li {
    display: inline-block;
    vertical-align: top;
    padding: 10px 20px 10px 0;
    margin: 0 -4px 0 0;
    width: 33.3%
}

.field_cover label,
.list-onethird li label {
    margin: 0;
    line-height: 1.6
}

.list-onethird.list-onethird--bg {
    background: rgba(0, 0, 0, .02);
    padding: 20px;
    border-radius: 2px
}



.listing--vertical li {
    font-size: 0.9rem;
    margin: 15px 0 0;
}

.listing--vertical li:first-child {
    margin: 0;
}



.floated-list {
    text-align: center;
}

.floated-list li {
    display: inline-block;
    vertical-align: top;
    padding: 10px;
}

.floated-list li a:hover {
    text-decoration: underline;
}



.selection-list {
    width: 100%;
    display: inline-block;
    margin: 0 0 20px;
}

.selection-list ul {
    margin: 0 -5px;
}

.selection-list ul li {
    width: 50%;
    display: inline-block;
    vertical-align: top;
    margin: 0 -2px;
    padding: 5px;
}

.selection-list ul li .selection {
    display: inline-block;
    width: 100%;
    border: 1px solid #e5e5e5;
    padding: 15px 15px 15px 40px;
    font-weight: 700;
    position: relative;
}

.selection-list ul li .selection small {
    line-height: 1.4;
    float: none !important;
    display: block;
    margin: 5px 0 0;
    text-transform: uppercase;
}

.selection-list ul li .selection .radio {
    vertical-align: top;
    position: absolute;
    left: 10px;
    top: 12px;
}

.selection-list ul li.is-active .selection {
    background: #f3f2f5;
    border-color: #f3f2f5;
}



.list-repeated {
    width: 100%;
    margin: 20px 0 0;
    display: inline-block;
}

.list-repeated .avtar {
    float: left;
    margin: 0 0 0;
    width: 40px;
    height: 40px;
}

.list-repeated .list-repeated__content {
    width: calc(100% - 40px);
    padding-left: 20px;
    float: left;
}

.list-repeated p {
    line-height: 1.2;
    margin: 0 0 5px;
    font-size: 1rem;
}


@media(max-width:1199px){
    .vertical-links li a {
        padding: 15px 20px;
    }
}

@media(max-width:767px){
    
    .list-onethird li {
        width: 50%
    }
}


