
.pagination.pagination--center {
    display: table;
    margin: 20px auto 0;
    text-align: center
}

.pagination li {
    display: inline-block;
    vertical-align: top;
    padding: 0;
    margin: 2px 1px
}

.pagination li a {
    text-transform: uppercase;
    width: 40px;
    line-height: 40px;
    text-align: center;
    height: 40px;
    display: block;
    font-size: .9rem;
    color: #333;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-weight: 600;
    position: relative;
    overflow: hidden;
    background: #fff
}

.pagination li.is-active a {
    background: var(--color-secondary);
    border-color: var(--color-secondary);
    color: #fff
}

.pagination li.is-disabled {
    opacity: .4
}

.pagination li.is-disabled a {
    cursor: not-allowed
}

.pagination li.is-prev a:before {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.right','value')}: 5px solid #000;
    margin: -5px 0 0 0; margin-#{theme('direction.left','value')}: -5px;
}

.pagination li.is-first a:before {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.left','value')}: 5px solid #000;
    margin: -5px 0 0 0; margin-#{theme('direction.left','value')}: -1px;
}

.pagination li.is-first a:after {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.right','value')}: 5px solid #000;
    margin: -5px 0 0 -6px
}

.pagination li.is-next a:before {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.left','value')}: 5px solid #000;
    margin: -5px 0 0 0; margin-#{theme('direction.left','value')}: -3px;
}


.pagination li.is-last a:before {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.left','value')}: 5px solid #000;
    margin: -5px 0 0 0; margin-#{theme('direction.left','value')}: -1px;
}

.pagination li.is-last a:after {
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    content: "";
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-#{theme('direction.right','value')}: 5px solid #000;
    margin: -5px 0 0 0; margin-left: -6px;
}



@media(max-width:theme('breakpoints.md','value')){
   
    .pagination li a {
        width: 38px
    }
}
