
/* Demo header  */

.sticky-demo-header .demo-header {
    background: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 99;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 0 2rem;
    line-height: 4rem;
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
  }
  .sticky-demo-header .demo-header .restore-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  .sticky-demo-header .demo-header .restore-wrapper > a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    padding: 10px 0;
  }
  .sticky-demo-header .demo-header .restore-wrapper .restore__counter {
    padding: 0px 8px;
    font-size: 1rem;
    color: var(--color-secondary);
    margin: 0.1rem 0;
    font-weight: 800;
    line-height: 1;
    letter-spacing: 4px;
  }
  .sticky-demo-header .demo-header .restore-wrapper .restore__progress {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 4px;
    width: 100%;
    overflow: hidden;
    font-size: 0.75rem;
    background-color: #e9ecef;
    border-radius: 2rem;
    margin: 0.25rem 0;
    max-width: 96px;
  }
  .sticky-demo-header .demo-header .restore-wrapper .restore__progress-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-color: var(--color-secondary);
    -webkit-transition: width 0.6s ease;
    transition: width 0.6s ease;
  }
  .sticky-demo-header .demo-header .restore-wrapper .restore__content {
    font-size: 0.675rem;
    color: #525252;
    font-weight: 600;
    margin-bottom: 0.25rem;
    line-height: 1.5;
  }
  .sticky-demo-header .demo-header .btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
  }
  .sticky-demo-header .demo-header .btn.btn-outline-primary {
    background: none;
    border-color: var(--color-secondary);
    color: var(--color-secondary);
    border: 1px solid;
  }
  .demo-cta {
    display: flex;
    align-items: center;
  }
  .sticky-demo-header .header {
    padding: 0;
  }
  .sticky-demo-header .main-bar {
    padding: 20px 0;
  }
  .sticky-demo-header .header {
    position: sticky;
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    z-index: 10;
    top: 0;
    width: 100%;
    background: #fff;
    border-bottom: none;
    border-bottom: solid 1px #dee2e6;
  }
  .sticky-demo-header .body {
    padding: 0;
  }
  .close-layer {
    z-index: 2;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
    -webkit-transition: all 300ms;
    transition: all 300ms;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .close-layer:hover {
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    opacity: 1;
  }
  .close-layer:before,
  .close-layer:after {
    position: absolute;
    left: 15px;
    content: " ";
    height: 18px;
    width: 2px;
    background-color: #000;
  }
  .close-layer:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .close-layer:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  .close-layer.close-layer--white:before,
  .close-layer.close-layer--white:after {
    background-color: #fff;
  }
  .demo-cta .close-layer {
    position: relative;
    right: auto;
    top: auto;
    -webkit-margin-start: 1rem;
    -moz-margin-start: 1rem;
    margin-left: 1rem;
  }
  
  @media (max-width: 1199px) {
    .sticky-demo-header .demo-header {
      display: none;
    }
  }