/* =====================================================================================================================
// Skin details */

.irs {
    height: 30px;
}
.irs-with-grid {
    height:60px;
     margin-top:10px;
}
.irs-line {
    height:6px; top:0;
    background: #e1e1e1;
   /* background: linear-gradient(to bottom, #DDD -50%, #FFF 150%);  W3C */
 /*   border: 1px solid #CCC;*/
    border-radius: 16px;
    -moz-border-radius: 16px;
}
    .irs-line-left {
        height: 8px;
    }
    .irs-line-mid {
        height: 8px;
    }
    .irs-line-right {
        height: 8px;
    }

.irs-bar {
    height:6px; top: 0;
   
    background:var(--color-secondary);
  /*  background: linear-gradient(to top, rgba(66,139,202,1) 0%,rgba(127,195,232,1) 100%);  W3C */
}
    .irs-bar-edge {
        height: 10px; top: 33px;
        width: 14px;
        border: 1px solid #428bca;
        border-right: 0;
        background: #428bca;
        background: linear-gradient(to top, rgba(66,139,202,1) 0%,rgba(127,195,232,1) 100%); /* W3C */
        border-radius: 16px 0 0 16px;
        -moz-border-radius: 16px 0 0 16px;
    }

.irs-shadow {
    height: 2px; top: 38px;
    background: #000;
    opacity: 0.3;
    border-radius: 5px;
    -moz-border-radius: 5px;
}
.lt-ie9 .irs-shadow {
    filter: alpha(opacity=30);
}

.irs-slider {
    top:-10px;
border-radius: 100%;


background: #fff;
color: #555555;
cursor: move;
font-weight: normal;
border: 1px solid #ddd;
cursor: move;
height: 1.8em;
position: absolute;
width: 1.8rem;
z-index: 2;
box-shadow: 0 5px 5px 5px rgba(0,0,0,0.06);
}
