  white-space: nowrap; }

.msg-list small {
  font-size: 95%; }

.msg-list__left {
  width: 50px; }

.window .avtar {
  width: 50px;
  height: 50px; }

.msg-list__right {
  width: calc(100% - 50px);
  padding-left: 15px; }

.message-details {
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  /*display: none*/
  z-index: 9; }

.message-container {
  padding: 20px 30px 30px;
  position: relative; }

.message-container .scrollbar {
  /*max-height: 350px;
    min-height: 350px;*/
  max-height: 320px;
  min-height: 320px; }

.message-head {
  padding: 15px 30px 15px;
  border-bottom: 1px solid #ddd;
  min-height: 80px;
  position: relative; }

.message-head h6,
.message-head p {
  line-height: 1; }

.message-head .msg-list {
  padding: 0;
  border-bottom: none; }

.message-row {
  width: 100%;
  display: inline-block;
  padding: 4px 0 0;
  margin: 0 0 0;
  /* border-top:1px solid #d6d9db;*/ }

.message-row .grid_1 {
  float: left;
  width: 50px;
  text-align: center;
  padding: 0 0; }

.message-row .grid_1 .avtar {
  display: block;
  margin: 0 auto 10px; }

.message-row .grid_1 .postedname {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: block; }

.message-row .grid_1 .postedname a {
  font-size: 15px;
  color: #0295fd; }

.message-row .grid_1 .postedname span {
  font-size: 13px;
  color: #999;
  display: block; }

.message-row .grid_2 {
  width: calc(100% - 50px);
  float: right;
  padding: 0 0 0 15px; }

.message-row .secionreviews {
  display: inline-block;
  padding: 15px;
  background: #f5f3dc;
  border-radius: 5px;
  position: relative;
  color: #333;
  margin: 0 0 10px; }

.message-row .secionreviews p {
  color: #333 !important;
  margin: 0;
  font-size: 1rem; }

.message-row .secionreviews:before {
  border-bottom: 10px solid transparent;
  border-top: 10px solid transparent;
  border-right: 10px solid #f5f3dc;
  position: absolute;
  left: -10px;
  top: 14px;
  content: ""; }

.message-row .datetext {
  text-transform: uppercase;
  font-size: 13px;
  color: #8a8a8a;
  padding: 0 0 5px 0;
  padding-left: 5px;
  display: block; }

.message-row.my-message .grid_1 {
  float: right; }

.message-row.my-message .grid_2 {
  float: left;
  padding: 0 15px 0 0; }

.message-row.my-message .datetext {
  float: right;
  clear: right; }

.message-row.my-message .secionreviews {
  float: right;
  clear: right;
  background: rgba(0, 0, 0, 0.04);
  border: none; }

.message-row.my-message .secionreviews:before {
  left: auto;
  right: -20px;
  border-left: 10px solid rgba(0, 0, 0, 0.04);
  border-right: 10px solid transparent; }

.message-row.my-message .secionreviews:after {
  display: none; }

.message-reply {
  padding: 10px 30px 0;
  border-top: 1px solid #ddd; }

.message-reply textarea {
  height: 70px; }

.message-reply .caption-wraper .field_label:empty {
  display: none; }

.window__search {
  position: relative;
  padding: 20px 15px;
  border-bottom: 1px solid #ddd;
  min-height: 80px; }

.window__search-field {
  border: 1px solid #ddd;
  height: 40px;
  width: 100%;
  position: relative;
  display: block;
  line-height: 36px;
  padding: 0 20px;
  color: #666;
  font-size: 1.1rem; }

.message-display--positioned.message-display {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%; }

.message-display--positioned.message-display .message-display__icon {
  opacity: 0.2;
  width: 100px;
  height: 100px;
  margin-bottom: 20px; }

.window__search-form {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background: #fff;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.1);
  z-index: 10;
  padding: 50px 20px 20px;
  display: none; }

.window__search-form .form .field_label:empty {
  display: none; }

.window__search-form .-link-close {
  z-index: 1; }

@media (max-width: 1023px) {
  .window {
    display: block; }
  .window__left {
    width: 100%; }
  .window__right {
    width: 100%;
    position: static; }
  .message-display--positioned.message-display {
    display: none; }
  .message-details {
    position: fixed;
    z-index: 20;
    overflow-y: auto;
    overflow-x: hidden; }
  html.show-message-details {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden; }
  .message-container {
    padding: 15px; }
  .message-reply {
    padding: 10px 15px 0; }
  .message-head {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: #fff;
    z-index: 10; }
  .message-reply {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 10; }
  .message-reply .row:last-child .field-set {
    margin-bottom: 0; }
  .window__left .scrollbar {
    min-height: 1%; }
  .message-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /*  height: calc(100% - 170px); */
    height: calc(100% - 225px);
    width: 100%;
    padding: 80px 0 0;
    overflow: auto; }
  .message-container .scrollbar {
    max-height: 650px; } }

@media (max-width: 767.98px) {
  .message-head {
    padding: 15px; }
  .message-head [class*="col-"]:first-child {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 83.333333%;
    flex: 0 0 83.333333%;
    max-width: 83.333333%; }
  .message-head [class*="col-"]:last-child {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 16.666667%;
    flex: 0 0 16.666667%;
    max-width: 16.666667%; }
  .message-reply textarea {
    height: 60px; } }
